import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import './VerticalSlider.css'


class VerticalSlider extends StreamlitComponentBase{
  public state = { value: 0 }

  public render = (): ReactNode => {
    const sliderLabel = this.props.args['label'];
    const sliderMin = this.props.args['min'];
    const sliderMax = this.props.args['max'];
    const sliderValue = this.props.args['value'];
    return (
        <>
        <div className="label">
          <h6 className="label-header">{sliderLabel}</h6>
          <p className="label-item">{this.state.value} dB</p>
        </div>
          <input type={'range'} 
                      defaultValue={sliderValue} 
                      min={sliderMin}
                      max={sliderMax} 
                      step = {1} 
                      onChange={(event) => this.onClicked(event.target.value)}
                      onMouseUp={this.onMouseUp}
                />
        </>
    )
  }

  /** Click handler for our "Click Me!" button. */
  private onClicked = (value:any): void => {
    this.setState(
      prevState => ({ value: value })
    )
  }


  private onMouseUp = (event:any): void => {
    Streamlit.setComponentValue(this.state.value)
  }

}


export default withStreamlitConnection(VerticalSlider)
